import YouTube from "react-youtube";
import io from "socket.io-client";
import { useEffect, useRef, useState } from "react";
import {
  Button,
  Comment,
  Divider,
  Form,
  Grid,
  GridColumn,
  Header,
  Label,
  Modal,
  Icon,
  Input,
  Checkbox,
} from "semantic-ui-react";
import Message from "./Message";
import User from "./User";

// const socket = io.connect("http://localhost:4000");
const socket = io.connect();

const App = () => {
  const [inputNickname, setInputNickname] = useState("");
  const [inputChat, setInputChat] = useState("");
  const [connectedNickname, setConnectedNickname] = useState("");
  const [chat, setChat] = useState([
    { username: "System", message: "Welcome!" },
  ]);
  const [userList, setUserList] = useState([]);
  const [syncOn, setSyncOn] = useState(false);
  const player = useRef(null);

  useEffect(() => {
    socket.on("login", ({ username, userList }) => {
      console.log(username);
      if (username) {
        setConnectedNickname(username);
      }
      setUserList(userList);
    });
  }, [userList]);

  useEffect(() => {
    socket.on("user joined", ({ username, userList }) => {
      console.log(username, userList);
      addChatMessage("System", username + " joined");
      setUserList(userList);
    });
  }, [chat, userList]);

  useEffect(() => {
    socket.on("user left", ({ username, userList }) => {
      console.log(username, userList);
      addChatMessage("System", username + " left");
      setUserList(userList);
    });
  }, [chat, userList]);

  useEffect(() => {
    socket.on("new message", ({ username, message }) => {
      addChatMessage(username, message);
    });
  }, [chat]);

  useEffect(() => {
    socket.on("sync", (data) => {
      console.log(data);
      if (!syncOn) {
        syncReceive(data);
      }
    });
  }, [syncOn]);

  useEffect(() => {
    const interval = setInterval(() => {
      if (syncOn) {
        syncSend();
      }
    }, 2000);

    return () => clearInterval(interval);
  }, [syncOn]);

  // useEffect(() => {
  //   return () => {
  //     socket.close();
  //   };
  // }, []);

  socket.on("connect", () => {
    console.log("connect");
  });

  const seekAndPlay = () => {
    player.current.internalPlayer.seekTo(10);
    player.current.internalPlayer.playVideo();
  };

  const syncSend = async () => {
    const currentTime = await player.current.internalPlayer.getCurrentTime();
    const statusCode = await player.current.internalPlayer.getPlayerState();

    const data = {
      currentTime,
      isPlaying: statusCode === 1,
    };
    socket.emit("sync", data);
  };

  const syncReceive = async ({ currentTime, isPlaying }) => {
    const mycCurrentTime = await player.current.internalPlayer.getCurrentTime();

    if (Math.abs(mycCurrentTime - currentTime) > 2) {
      player.current.internalPlayer.seekTo(currentTime);
    }

    if (isPlaying) {
      player.current.internalPlayer.playVideo();
    } else {
      player.current.internalPlayer.pauseVideo();
    }
  };

  const addUser = (username) => {
    socket.emit("add user", username);
  };

  const getCurrentTimeFunc = async () => {
    console.log(await player.current.internalPlayer.getCurrentTime());
  };

  const addChatMessage = (username, message) => {
    setChat([{ username, message }, ...chat].slice(0, 50));
  };

  const sendChatMessage = (message) => {
    if (message) {
      setInputChat("");
      addChatMessage(connectedNickname, message);
      socket.emit("new message", message);
    }
  };

  const renderChat = () => {
    return chat.map(({ username, message }, index) => (
      <Message username={username} message={message} key={index} />
    ));
  };

  const renderUserList = () => {
    return userList.map((username, index) => (
      <User username={username} key={index} />
    ));
  };

  return (
    <>
      <Grid textAlign="center" verticalAlign="middle" columns="equal">
        <Grid.Row></Grid.Row>
        <Grid.Row>
          <Grid.Column>
            <YouTube
              videoId="8zxBfLGX7-Y"
              ref={player}
              opts={{ width: "1000px", height: "600px" }}
              // onPause={(e) => console.log(e.target.getPlayerState)}
            />
          </Grid.Column>
        </Grid.Row>

        <Grid.Row>{renderUserList()}</Grid.Row>

        <Grid.Row>
          <Checkbox
            label="Sync with my current video"
            onChange={() => setSyncOn(!syncOn)}
            checked={syncOn}
          ></Checkbox>
        </Grid.Row>

        <Grid.Row>
          <Comment.Group style={{ width: "400vh" }}>
            <Header as="h3" dividing>
              Chats
            </Header>

            <Form>
              <Input
                action={{
                  icon: "send",
                  onClick: () => sendChatMessage(inputChat.trim()),
                }}
                fluid
                onChange={(e) => setInputChat(e.target.value)}
                value={inputChat}
              ></Input>
            </Form>

            {renderChat()}
          </Comment.Group>
        </Grid.Row>

        {/* <Button onClick={() => addUser()}>login</Button>

        <Button size="massive" onClick={() => seekAndPlay()}>
          seekAndPlay
        </Button>
        <Button onClick={() => getCurrentTimeFunc()}>getCurrentTime</Button> */}
      </Grid>

      <Modal basic open={!connectedNickname}>
        <Header icon>
          <Icon name="chat" />
          Please Enter the Nickname
        </Header>

        <Form>
          <Input
            action={{
              content: "Connect",
              icon: "sign-in",
              onClick: () => {
                if (inputNickname) {
                  addUser(inputNickname);
                }
              },
            }}
            required
            fluid
            onChange={(e) => setInputNickname(e.target.value)}
            value={inputNickname}
          ></Input>
        </Form>
      </Modal>
    </>
  );
};

export default App;
