import YouTube from "react-youtube";
import io from "socket.io-client";
import { useEffect, useRef, useState } from "react";
import {
  Button,
  Comment,
  Divider,
  Form,
  Grid,
  GridColumn,
  Header,
  Label,
  Modal,
  Icon,
  Input,
} from "semantic-ui-react";

const User = ({ username }) => {
  const isSystem = username === "System";

  return (
    <Label image>
      {isSystem && (
        <img src={`https://avatars.dicebear.com/api/identicon/System.svg`} />
      )}
      {!isSystem && (
        <img
          src={`https://avatars.dicebear.com/api/adventurer-neutral/${username}.svg`}
        />
      )}
      {username}
    </Label>
  );
};

export default User;
